<template>
  <div class="login">
    <div class="login-box">  
       <div class="logo">
         <img src="../assets/images/snipaste1.png" alt="" class="auto-img">
          <div class="close-box" @click="gohome">
            <svg
              t="1596705973022"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2063"
              width="200"
              height="200"
            >
              <path
                d="M298.666667 586.666667a74.666667 74.666667 0 1 1 0-149.333334 74.666667 74.666667 0 0 1 0 149.333334z m213.333333 0a74.666667 74.666667 0 1 1 0-149.333334 74.666667 74.666667 0 0 1 0 149.333334z m213.333333 0a74.666667 74.666667 0 1 1 0-149.333334 74.666667 74.666667 0 0 1 0 149.333334z"
                p-id="2064"
                fill="#bfbfbf"
              />
            </svg>
          </div>
       </div>

      <div class="login-form">
        <div class="login-form-box">
          <van-form>
            <van-field  placeholder="请输入手机号" v-model="userlogininfo.phone" />
            <van-field
              :type="ispassword ? 'password':'text'"
              
              placeholder="请输入密码"
              v-model="userlogininfo.password"
              :right-icon="ispassword ? 'closed-eye' : 'eye-o'"
              @click-right-icon="password"
            />
            <div class="clearfix forgot">
              <span class="fr">忘记密码?</span>
            </div>
            <div class="login-btn">
              <van-button round block type="info" native-type="submit" @click="login" color="#5f56ff">登 录</van-button>
            </div>
            <div class="login-btn">
              <van-button round block type="default" @click="tangchu" color="rgb(187, 183, 183)">注 册</van-button>
            </div>
          </van-form>
        </div>
      </div>
    </div>

    <van-popup position="bottom" v-model="isopen" closeable class="popup">
      <div class="popup-item">
        <div class="register-text">注册</div>
        <van-form>
           <van-field label="昵称" placeholder="1-10位昵称" v-model="userRegisterInfo.nickName" />
          <van-field label="手机号" placeholder="11位手机号" v-model="userRegisterInfo.phone" />
          <van-field
            :type="ispassword ? 'password':'text'"
            label="密码"
            placeholder="6-16位密码且以字母开头"
            v-model="userRegisterInfo.password"
            :right-icon="ispassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="password"
          />
          <div class="login-btn">
            <van-button round block type="info" native-type="submit" @click="register" color="#5f56ff">注  册</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/login.less";
import {validForm} from '../assets/js/validForm';
import {utils} from '../assets/js/utils';

export default {
  data() {
    return {
      userlogininfo: {
        phone: "",
        password: "",
      },
      ispassword: true,
      isopen: false,
      userRegisterInfo: {
        phone: "",
        nickName: "",
        password: "",
      },
    };
  },
  methods: {
    //跳到home页面
    gohome() {
     this.$router.push({name:'Home'})
    },
    // 密码显示隐藏
    password() {
      this.ispassword = !this.ispassword;
    },
    // 弹出层
    tangchu() {
      this.isopen = true;
    },

    // 注册
    register(){
      //获取用户注册信息
      let userInfo=this.userRegisterInfo;
      let o={
          phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: '手机号格式不正确'
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: '昵称格式不正确'
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: '密码格式不正确'
        }  
      }

      if(!validForm.valid(o)){
        return;
      }

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        loadingType: 'spinner',
        duration: 0,
      })

     
      let info=Object.assign({},userInfo);
      info.appkey=this.appkey;
      let data=utils.queryString(info);
      
      this.axios({
        method:'POST',
        url:'/register',
        data
      }).then(result=>{
        this.$toast.clear();
        

        //  如果注册成功
        if (result.data.code == 100) {
          this.isRegister = false;
          this.isopen = false;
          //清空注册表单的数据
          for (let key in userInfo) {
            userInfo[key] = '';
          }
           this.$notify({ background: '#5f56ff', message: '注册成功' })
        } else {
          //如果注册失败,则提示
          this.$notify({
            type: 'warning', 
            message: result.data.msg
          })
        }
      }).catch(err=>{
        this.$toast.clear();
        
      })

    },

    //登录
    login(){
      let userInfo = this.userlogininfo;

       let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: '手机格式不正确'
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: '密码格式不正确'
        }
      };

      //验证表单
      if (!validForm.valid(o)) {
        return;
      }

       this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        loadingType: 'spinner',
        duration: 0,
      })

      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);

      this.axios({
        method: 'POST',
        url: '/login',
        data
      }).then(result => {
        this.$toast.clear();
        // 
        if (result.data.code == 200) {
          localStorage.setItem('CSTK', result.data.token);
          this.$router.push({name: 'Home'});
          // 
        } else {
          this.$notify({
            type: 'warning', 
            message: result.data.msg
          })
        }
      }).catch(err => {
        this.$toast.clear();
        

      })

    }
  },
};
</script>

<style lang="less" scoped>
</style>